<template>
  <div>
    <!-- 添加待簽 -->
    <a-card title="HC預新增">
      <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.hcid===null">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
    </a-card>
    <a-divider dashed />
    <!-- 修改待簽 -->
    <a-card title="HC預修改" >
      <div>
        <a-list
          class="demo-loadmore-list"
          item-layout="horizontal"
          v-bind:data-source="data"
          size="small"
        >
          <a-list-item slot="renderItem" slot-scope="item"  v-if="item.hcid!==null">
            <a slot="actions" style="color:green" @click="sub(item)">OK</a>
            <a slot="actions" style="color:red"  @click="delhcy(item.id)">NG</a>
            <a-list-item-meta>
              <div slot="title">
                機種：{{ item.model }}
                <a-divider type="vertical" />
                階段：{{ item.build }}
                <a-divider type="vertical" />
                no：{{ item.sno }}
                <a-divider type="vertical" />
                站位名：{{ item.sname }}
                <a-divider type="vertical" />
                QPL：{{ item.qpl }}
                <a-divider type="vertical" />
                Process：{{ item.process }}
               </div>
            </a-list-item-meta>
          </a-list-item>
        </a-list>
      </div>
    </a-card>
  </div>
</template>
<script>

import {list,savey,del} from "@/api/fix/hcy";
import {save} from "@/api/fix/hc";
import security from '@/utils/security'
export default {
  data() {
    return {
      data: [],
      formData: {
         model: localStorage.getItem("model"),
      },

    };
  },
  methods: {
    findSubmit(){
      list(this.formData).then((res) => {
        this.data = res.data;
      });
    },
    sub(data){
        var hcyid=data.id;
        data.id=data.hcid;
      save(data).then((res) => {
          if (res.code == 200&&data.id===null) {
          this.$message.success("添加成功");
          }
          else{
          this.$message.success("修改成功");
          }
      });
      del(hcyid).then((res) => {
        this.findSubmit()
      })
    },
    delhcy(id){
      del(id).then((res) => {
        this.findSubmit()
      })
    }

  },
  created: function () {
    list(this.formData).then((res) => {
      this.data = res.data;
    });
  },
};
</script>