import request from '@/utils/request'

// 分页查询HC信息
export function pageList(query) {
  return request({
    url: '/fixHc/pageList',
    method: 'get',
    params: query,
  })
}

// 不分页查询HC信息
export function list(data) {
  return request({
    url: '/fixHc/list',
    method: 'post',
    data: data
  })
}

// 添加HC信息
export function insert(data) {
  return request({
    url: '/fixHc/insert',
    method: 'post',
    data: data
  })
}

// 修改HC信息
export function update(data) {
  return request({
    url: '/fixHc/update',
    method: 'put',
    data: data,
  })
}

// 添加或修改HC信息
export function save(data) {
  return request({
    url: '/fixHc/save',
    method: 'post',
    data: data
  })
}

// 删除HC信息
export function del(id) {
  return request({
    url: '/fixHc/'+id,
    method: 'delete'
  })
}

// 根据id查询HC信息
export function onebyid(id) {
  return request({
    url: '/fixHc/'+id,
    method: 'get'
  })
}

// HC文件上傳
export function upload(data) {
    return request({
      url: '/fixHc/upload',
      method: 'post'
    })
  }

// HC文件下載
export function exports() {
  return request({
    url: '/fixHc/export',
    method: 'post',
    responseType: 'blob'
  })
}